html{
  background: url(./assets/corkBoard.jpg);
  background-size: contain;
  background-repeat: repeat;
  font-size: 62.5%;
}

/* wrapper styles */
.wrapper{
  max-width: 1200px;
  margin-top: 50px;
}

main{
  display: flex;
  min-height: 100vh;
  max-height: 100%;
}

/* .showImages{
  position: absolute;
  top: 10px;
  left: 10px;
}

.viewAll{
  position: absolute;
