  left: 100px;
} */

/* format notes section */
.notes{
  width: 678px;
  margin-left: 2%;
}

/* formate photos section */
.photos{
  width: calc(95% - 678px);
  margin-right: 2%;
}

/* WELCOME message dialog */
.welcome {
  top: 15vh;
  width: 600px;
  font-size: 2rem;
  background: white;
}

.welcome button{
  font-family: cursive;
  font-weight: bolder;
}

.welcome .titleBar{
  background: #2196F3;
}

/* Add padding around text in welcome message */
.welcome li{
  padding: 0;
}

.welcome ul{
  display: block;
  padding: 0 15px;
}
.welcome p{
  padding: 0 15px;
}

/* Quick Notes Title in welcome message */
h1{
  font-size: 3.5rem;
  text-align: center;
  padding: 0;
  font-family: 'calibri', sans-serif;
}

/* Format add new note dialog pop up */
dialog{
  position: absolute;
  top: 30vh;
  width: 300px;
  padding: 0;
  z-index: 10;
}

form{
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* Format Sticky Note */
textarea {
  background: rgb(250, 237, 166);
  resize: none;
  border: none;
  font-family: 'calibri', sans-serif;
  padding: 13.5px 15px;
  font-size: 1.75rem;
  height: 200px;
  width: 212px;
}

form textarea{
  width: 100%;
}

form button{
border: none;
background: rgb(248, 228, 137);
}

.titleBar{
  background: rgb(248, 228, 137);
  display: flex;
  justify-content: flex-end;
}

.titleBar .delete,
form button{
  padding: 5px 10px;
  font-family: cursive;
  font-weight: bold;
  display: block;
  cursor: pointer;
  color: #282c34a4;
  width: 30px;
  font-size: 1.5rem;
  border: none;
  background: rgb(248, 228, 137);
}

.titleBar .delete:hover,
.titleBar .delete:focus,
form button:hover,
form button:focus{
  background: rgb(230, 207, 109);
}

form .titleBar{
  justify-content: space-between;
}

form button[type=submit]{
  font-family: "Calibri", sans-serif;
  width: 110px;
  align-self: flex-end;
  font-size: 1.75rem;
}

/* Format Photos List */
ul{
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: flex-end;
  list-style: none;
  padding: 0;
}

.notes li{
  padding: 6.5px;
}

.photos li{
  padding: 6px;
}

img{
  max-height: 199px;
  max-width: 278px;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
}

/* File upload button */
button[type=open],
.customUpload{
  font-weight: bolder;
  background:white;
  cursor: pointer;
  color: #282c34a4;
  border: none;
  border-radius: 35px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.603);
  transition: 0.3s;
}

button[type=open]{
  padding: 6px 14px;
  font-size: 3.5rem;
  padding-top: 0;
  position: fixed;
  right: 15px;
  top: 200px;
}

/* Hover and focus states for upload photo and new note buttons */
button[type=open]:hover,
button[type=open]:focus,
input.customUpload:hover + label,
input.customUpload:focus + label{
  color: #2196F3;
}

/* Visually hide toggle checkbox, input file upload and labels */
.visuallyHidden { 
  position: absolute !important;
  height: 1px; 
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; /* added line */
}

.customUpload{
  font-size: 2.75rem;
  cursor: pointer;
  padding: 7px 13px;
  top: 100px;
  right: 15px;
  position: fixed;
}

/* Toggle Theme */
/* Container holding the switch button */
.switch {
  position: absolute;
  display: inline-block;
  top:15px;
  right: 15px;
  width: 60px;
  height: 34px;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2196F3;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #ccc;
  box-shadow: 0 0 3px rgb(138, 138, 138);
}

input + .slider {
  box-shadow: 0 0 3px rgb(26, 115, 187);
}

input:hover + .slider::before,
input:focus + .slider::before{
  box-shadow: 0 0 5px rgb(37, 37, 37);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Round sliders */
.slider {
  border-radius: 34px;
}

.slider:before {
  border-radius: 50%;
}

input[type=file]{
  padding: 15px;
}

/* footer section */
footer{
  position: fixed;
  bottom: 60px;
  right: -60px;
  transform: rotate(90deg);
}

/* Responsive */
@media (max-width: 1250px){
  .notes{
    width: 452px;
  }

  .photos{
    width: calc(95% - 460px);
  }

  li{
    padding: 7px;
  }

}

@media (max-width: 830px){
  .notes {
    width: 226px;
  }

  .photos{
    width: calc(94% - 226px);
  }
}

@media (max-width: 630px){
  .welcome{
    width: 500px;
  }
}

@media (max-width: 550px){
  main{
    flex-direction: column;
    align-items: center;
  }

  .notes,
  .photos{
    margin-left: 0;
    margin-right: 0;
  }

  .photots{
    margin-top: 0;
  }

  .notes li:last-of-type{
    padding-bottom: 0;
  }

  .photos li:first-of-type{
    padding-top: 0;
  }

  .notes li{
    padding: 8px;
  }

  .photos li{
    padding: 7px;
  }

  .welcome{
    width: 300px;
    top: 5vh
  }

  .newNote{
    width: 300px;
    top: 10vh;
  }

  ul{
    justify-content: center;
  }

  h1{
    font-size: 3rem;
  }

  .welcome p,
  .welcome li{
    font-size: 1.5rem;
  }

  .photos{
    margin-top: 0;
  }

.customUpload{
  font-size: 2rem;
  padding: 5px 9px;
}

button[type=open]{
  font-size: 2.5rem;
  padding: 5px 10px;
  padding-top: 0;
}

}